
:root {
 --primary: #545E75;
 --secondary: #63ADF2;
 --tertiary: #a7cced;
 --cuaternary: #304d6d;
 --fifth: #82a0bc;
}
* {
    color: var(--tertiary);
    font-family: 'Josefin Sans', sans-serif;
}

body {
    background-color: var(--cuaternary);
    padding: 50px;
}

.container-header {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
}

.container-header h1 {
    margin-bottom: 0;
    width: 50%;
}
.container-header form {
    width: 50%;
    padding-right: 10px;
}

hr {
    border-color: var(--tertiary);
}

input {
    background-color: var(--tertiary);
    border: 1px solid  var(--cuaternary);
    border-radius: 5px;
    color: var(--cuaternary);
    font-size: 1rem;
    font-weight: 600;
    padding: 5px;
    width: 100%;
}

.grid-card {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.card {
    border: 1px solid var(--tertiary);
    border-radius: 5px;
    margin: 15px;
    width: 18%;
    overflow: hidden;
    text-align: center;
}

.card p {
    padding: 10px;
}

.card img {
    height: 150px;
    overflow: hidden;
    width: 100%;
}

.category {
    margin-top: 50px;
}